import { _Main_Template } from "./_Main_Template";

export function Main_Home() {
    return <_Main_Template left={Left()} right={Right()} body={Body()}></_Main_Template>;
}

const Left = () => {
    return <div></div>;
};
const Right = () => {
    return <div></div>;
};

const Body = () => {
    return (
        <div
            style={{
                flex: "1 1",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
                textAlign: "center",
                lineHeight: "30px",
            }}
        >
            <div>Houdini로의 여정에 오신 것을 환영합니다!</div>
            <div>
                이 블로그는 Houdini 노드와 예제에 관한 다양한 정보를 공유하며, 사용자들의 기여로 함께 성장하고 있습니다.
            </div>
            <div>
                초보자부터 전문가까지, 누구나 이해하기 쉽도록 실습 예제와 노드 중심의 콘텐츠를 제공하여 Houdini의 강력한
                기능을 쉽게 익힐 수 있도록 돕습니다.
            </div>
            <div>유저들이 직접 참여해 자료를 보완하고 새로운 팁을 공유할 수 있는 열린 공간입니다.</div>
            <div>함께 Houdini의 무한한 가능성을 탐구하며, 더 나은 학습 환경을 만들어 보세요!</div>
            <br></br>
            <a href="https://open.kakao.com/o/gTNipfkg"> 카카오톡 오픈채팅 (질문, 기여하기) </a>
        </div>
    );
};
